import humanId from "human-id"
import range from "lodash/range"
import {
  createGameServer,
  createRuneSDKForLogicRunner,
  RunGameLogic,
} from "rune-games-sdk/dist/multiplayer/gameServer"
import { PlayerId, Players } from "rune-games-sdk/multiplayer"
import * as uuid from "uuid"

import { emitter } from "./emitter"
import { createPromiseQueue } from "./queue"

export const users = range(0, 4).map(() => ({
  id: Math.round(Math.random() * 1e5),
  displayName: humanId({
    adjectiveCount: 0,
    capitalize: false,
    separator: "-",
  }),
}))

const userIdToPlayerId: Map<number, PlayerId> = new Map()
const players: Players = {}

// only call this after the game client state is PLAYING
export function isSpectator(userId: number) {
  const playerId = userIdToPlayerId.get(userId)

  return !!(playerId && !players[playerId])
}

const queue = createPromiseQueue()

export async function startGameServer(gameUrl: string) {
  const indexHtml = await fetch(gameUrl).then((r) => r.text())

  const dom = document.createElement("div")
  dom.innerHTML = indexHtml
  const logicJsSrc = dom
    .querySelector('script[src$="logic.js"]')
    ?.getAttribute("src")

  if (!logicJsSrc) throw new Error("logic.js script not found")

  const logicJsUrl = new URL(logicJsSrc, gameUrl).toString()

  const logicJs = await fetch(logicJsUrl).then((r) => r.text())

  const Rune = createRuneSDKForLogicRunner()

  eval(logicJs)

  const { setup, events, actions, context: runeContext, gameConfig } = Rune

  if (!setup || !events || !actions || !gameConfig) {
    throw new Error("sdk init failed")
  }

  const logicRunner: RunGameLogic = {
    getConfig: () => gameConfig,
    runSetup: async (_, players) => setup(players),
    runEvent: async (_, event, params, context) => {
      const eventHandler = events[event]

      if (!eventHandler) {
        throw new Error("INVALID EVENT!")
      }

      return {
        game: eventHandler(params, context),
        context: runeContext,
      }
    },
    runAction: async (_, action, ...args) => ({
      game: actions[action](...args),
      context: runeContext,
    }),
  }

  const msgLogger = console

  function addPlayer(userId: number) {
    const user = users.find((u) => u.id === userId)

    if (!user) throw new Error("user not found")

    const playerId = uuid.v4()
    userIdToPlayerId.set(userId, playerId)
    players[playerId] = { playerId, displayName: user.displayName }

    return playerId
  }

  function removePlayer(userId: number) {
    const playerId = userIdToPlayerId.get(userId)

    if (!playerId) throw new Error("player not found")

    userIdToPlayerId.delete(userId)
    delete players[playerId]

    return playerId
  }

  range(0, gameConfig.minPlayers).map((i) => {
    addPlayer(users[i].id)
  })

  const gameServer = createGameServer({
    initialMsgLogger: msgLogger,
    network: {
      getPlayers: () => players,
      broadcastMsg: (_, msg) => emitter.emit("broadcastMsg", { msg }),
      sendMsg: (_, playerId, msg) => {
        const userId = [...userIdToPlayerId.entries()].find(
          ([_, pid]) => playerId === pid
        )?.[0]

        if (!userId) throw new Error("user not found")

        emitter.emit("emitMsg", { userId, msg })
      },
    },
    logic: logicRunner,
    initialGameStateAndContext: null,
  })

  emitter.addListener("connect", ({ userId }) => {
    queue(async () => {
      if (userIdToPlayerId.has(userId)) return

      const playerCount = Object.keys(players).length
      // todo: maybe better to handle this inside SDK?
      const isSpectator =
        playerCount >= gameConfig.maxPlayers ||
        (!gameConfig.eventCallbacks.playerJoined &&
          playerCount >= gameConfig.minPlayers)

      // todo: handle case of min 2, second leave then rejoin

      if (isSpectator) {
        // todo: this should not be needed
        const playerId = uuid.v4()
        userIdToPlayerId.set(userId, playerId)

        return
      }

      const playerId = addPlayer(userId)

      await gameServer.then((gameServer) =>
        gameServer.onPlayerJoined(msgLogger, playerId)
      )
    })
  })

  emitter.addListener("disconnect", ({ userId }) => {
    queue(async () => {
      const playerId = removePlayer(userId)

      await gameServer.then((gameServer) =>
        gameServer.onPlayerLeft(msgLogger, playerId)
      )
    })
  })

  emitter.addListener("onMsg", ({ userId, msg }) => {
    queue(async () => {
      const playerId = userIdToPlayerId.get(userId)

      if (!playerId) throw new Error("player not found")

      await gameServer.then((gameServer) =>
        gameServer.onMsg(msgLogger, playerId!, msg)
      )
    })
  })

  return gameConfig
}
