import { useCallback } from "react"

export function useIsLastConnectedPlayerView(
  playerViews: { userId: number; connected: boolean }[]
) {
  return useCallback(
    (playerView: typeof playerViews[number]) =>
      playerView.connected &&
      playerViews.filter((p) => p.connected).length === 1,
    [playerViews]
  )
}
